<template>
  <div>
    <a-card style="background-color:#fff8db;margin-bottom:10px" v-show="show">
      <div class="table-page-search-wrapper">
        <a-form class="ant-advanced-search-form searchFixed" >
          <a-row :gutter="32">
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;">
              <a-form-item :label="$t('dept.index')" >
                <dept-select ref="dept" @change="(value)=>{value?queryParam.deptId=value:delete queryParam.deptId; this.queryData()}"/>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;">
              <a-form-item :label="this.$t('vee.datatype')">
                <a-select v-model="datetype">
                  <a-select-option value="0">
                    {{ $t('dct.REPEAT_TYPE3') }}
                  </a-select-option>
                  <a-select-option value="1">
                    {{ $t('dct.REPEAT_TYPE4') }}
                  </a-select-option>
                </a-select>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;" v-show="datetype=='0'">
              <a-form-item :label="$t('common.time')" >
                <pick-date :defaultValue="nowTime" @change="onChangeTime" :allowClear="false"></pick-date>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;" v-show="datetype=='1'">
              <a-form-item :label="$t('common.time')" >
                <pick-month :defaultValue="nowTime1" @change="onChangeTime" :allowClear="false"></pick-month>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:30px;">
              <a-form-item >
                <span>
                  <btn-search @click="queryData" :showInput="false"></btn-search>&nbsp; &nbsp;
                </span>
              </a-form-item>
            </a-col>
          </a-row>
        </a-form>
      </div>
    </a-card>
    <a-card>
      <div class="table-page-search-wrapper" style="margin-bottom: 10px;">
        <a-row>
          <a-col :md="19" :sm="19">
          </a-col>
          <a-col :md="5" :sm="5">
            <sorter @refresh="queryData" @show="showSearch"></sorter>
          </a-col>
        </a-row>
      </div>
      <div style="width:50%;float:left;padding:20px,0px"> 
        <div style="color:#12AAEB;font-size:16px;height:40px;line-height:40px">  {{ $t('loss.ana1') }}</div>
        <s-table 
          ref="table" 
          rowKey="item"
          :data="loadData" 
          :bordered="true" 
        > 
          <a-table-column
            :title="$t('loss.rate')"
            width="150px"
            dataIndex="item"
            align="center"
          >
          </a-table-column>
          <a-table-column
            :title="$t('loss.decount')"
            width="150px"
            dataIndex="value"
            align="center"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('loss.par')"
            width="150px"
            dataIndex="per"
            align="center"
          >
          </a-table-column>
        </s-table>
        <div style="color:#12AAEB;font-size:16px;height:40px;line-height:40px;margin-top:20px">  {{ $t('loss.ana2') }}</div>
        <s-table 
          ref="table1" 
          rowKey="item"
          :data="loadData1" 
          :bordered="true" 
        > 
          <a-table-column
            :title="$t('loss.rate')"
            width="150px"
            dataIndex="item"
            align="center"
          >
          </a-table-column>
          <a-table-column
            :title="$t('loss.decount')"
            width="150px"
            dataIndex="value"
            align="center"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('loss.par')"
            width="150px"
            dataIndex="per"
            align="center"
          >
          </a-table-column>
        </s-table>
      </div>
      <div style="width:50%;float:left;padding:10px">
        <div style="height:300px;margin-top:20px"> 
          <v-chart :forceFit="true" :height="height" :data="data" :scale="scale">
            <v-tooltip :showTitle="false" dataKey="item*percent" />
            <v-axis />
            <v-legend dataKey="item" />
            <v-pie position="percent" color="item" :vStyle="pieStyle" :label="labelConfig" />
            <v-coord type="theta" :radius="0.75" :innerRadius="0.6" />
          </v-chart>
        </div>
        <div style="height:300px;margin-top:10px"> 
          <v-chart :forceFit="true" :height="height" :data="data1" :scale="scale">
            <v-tooltip :showTitle="false" dataKey="item*percent" />
            <v-axis />
            <v-legend dataKey="item" />
            <v-pie position="percent" color="item" :vStyle="pieStyle" :label="labelConfig" />
            <v-coord type="theta" :radius="0.75" :innerRadius="0.6" />
          </v-chart>
        </div>
      </div>
    </a-card>
  </div>
</template>
<script>
  import STable from '@/components/table/'
  import { axios } from '@/utils/request'
  import Dict from '@/views/public/Dict'
  import { toUTC } from '@/inhe/picker/utils'
  import DeptSelect from '@/views/public/DeptSelect'
  const DataSet = require('@antv/data-set')
  export default {
    name: 'Index',
    components: {
      STable,
      Dict,
      DeptSelect,
    },
    data() {
      return {
        show:true,
        queryParam: {},
        datetype:'0',
        data:[],
        data1:[],
        scale : [{
          dataKey: 'percent',
          min: 0,
          formatter: '.0%',
        }],
        pieStyle: {
          stroke: '#fff',
          lineWidth: 1,
        },
        labelConfig: ['percent', {
          formatter: (val, item) => {
            return item.point.item + ': ' + val
          }
        }],
        height:'300',
        nowTime1:toUTC(new Date()),
        nowTime: toUTC(new Date(new Date().getTime()-24*3600*1000)),
        loadData:  parameter => { 
          const that = this
          if(that.datetype=='0'){
            parameter.nowTime = that.nowTime.slice(0,10)
          }else{
            parameter.nowTime = that.nowTime1.slice(0,7)
          }
          parameter.dataType = that.datetype    
          parameter.way = 1
          return axios({
            url: '/loss/analysis/search',
            method: 'get',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => { 
            
            //res.result.data=[{'<0':0,'[0,5)':0,'[5,10)':0,'[10,60)':20,'>60':10}]
            if(res.result.data.length==0){
             return res.result
            }
            res.totalCount=4
            res.totalPage=1
            var redata=res.result.data[0]
            var count=redata['<0']+redata['[0,5)']+redata['[5,10)']+redata['[10,60)']+redata['>60']
            count=count==0?1:count
            var data1=[
              {item:'<0',value:redata['<0'],per:parseFloat(redata['<0']*100/count).toFixed(2)+'%'},
              {item:'[0,5)',value:redata['[0,5)'],per:parseFloat(redata['[0,5)']*100/count).toFixed(2)+'%'},
              {item:'[5,10)',value:redata['[5,10)'],per:parseFloat(redata['[5,10)']*100/count).toFixed(2)+'%'},
              {item:'[10,60)',value:redata['[10,60)'],per:parseFloat(redata['[10,60)']*100/count).toFixed(2)+'%'},
              {item:'>60',value:redata['>60'],per:parseFloat(redata['>60']*100/count).toFixed(2)+'%'},

            ]
            res.result.data=data1
            this.getpeiData1(redata)
            return res.result
          })
        },
        loadData1:  parameter => { 
          const that = this
          if(that.datetype=='0'){
            parameter.nowTime = that.nowTime.slice(0,10)
          }else{
            parameter.nowTime = that.nowTime1.slice(0,7)
          }
          parameter.dataType = that.datetype 
          parameter.way = 2 
          return axios({
            url: '/loss/analysis/search',
            method: 'get',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => { 
           
           // res.result.data=[{'<0':10,'[0,5)':20,'[5,10)':30,'[10,60)':20,'>60':10}]
           if(res.result.data.length==0){
             return res.result
           }
            res.totalCount=4
            res.totalPage=1
            var redata=res.result.data[0]
            var count=redata['<0']+redata['[0,5)']+redata['[5,10)']+redata['[10,60)']+redata['>60']
            count=count==0?1:count
            var data1=[
              {item:'<0',value:redata['<0'],per:parseFloat(redata['<0']*100/count).toFixed(2)+'%'},
              {item:'[0,5)',value:redata['[0,5)'],per:parseFloat(redata['[0,5)']*100/count).toFixed(2)+'%'},
              {item:'[5,10)',value:redata['[5,10)'],per:parseFloat(redata['[5,10)']*100/count).toFixed(2)+'%'},
              {item:'[10,60)',value:redata['[10,60)'],per:parseFloat(redata['[10,60)']*100/count).toFixed(2)+'%'},
              {item:'>60',value:redata['>60'],per:parseFloat(redata['>60']*100/count).toFixed(2)+'%'},

            ]
            res.result.data=data1
            this.getpeiData2(redata)
            return res.result
          })
        },
      }
    },
    methods: {
      getpeiData1(redata){
        const that=this
        that.data=[]
        var sourceData=[]
        sourceData=[
          {item:'<0',count:redata['<0'],},
          {item:'[0,5)',count:redata['[0,5)']},
          {item:'[5,10)',count:redata['[5,10)']},
          {item:'[10,60)',count:redata['[10,60)']},
          {item:'>60',count:redata['>60']},
        ]
        const dv = new DataSet.View().source(sourceData)
        dv.transform({
          type: 'percent',
          field: 'count',
          dimension: 'item',
          as: 'percent'
        })
        that.data = dv.rows
      },
      getpeiData2(redata){
        const that=this
        that.data1=[]
        var sourceData=[]
        sourceData=[
          {item:'<0',count:redata['<0'],},
          {item:'[0,5)',count:redata['[0,5)']},
          {item:'[5,10)',count:redata['[5,10)']},
          {item:'[10,60)',count:redata['[10,60)']},
          {item:'>60',count:redata['>60']},
        ]
        const dv = new DataSet.View().source(sourceData)
        dv.transform({
          type: 'percent',
          field: 'count',
          dimension: 'item',
          as: 'percent'
        })
        that.data1 = dv.rows
      },
      onChangeTime(date){
        date=toUTC(new Date(new Date(date).getTime()+8*3600*1000))
         if(this.datetype=='0'){
           this.nowTime=date
         }else{
           this.nowTime1=date
         }
      },
      showSearch() {
        this.show = !this.show
      },
      //输入任务名称的search,上方列表表刷新
      queryData(){
        this.$refs.table.refresh()
        this.$refs.table1.refresh()
      }
    }
  }
</script>
<style lang="scss" scoped>
.headtab{
  background: #fff;
}
.ant-tabs>.ant-tabs-bar{
  margin: 0 !important;
}
.ant-form-item-children .ant-calendar-picker{
  width: 100%;
}
</style>